import React from 'react';
import MaterialTable from 'material-table';
import { forwardRef } from 'react';
import CreateProduct from './createProduct'
import AddBox from '@material-ui/icons/AddBox';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import axios from 'axios';
import {connect} from 'react-redux';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

const tableIcons = {
    Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
    Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
    DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
    Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef((props, ref) => <ArrowUpward {...props} ref={ref} />),
    ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
  };

class MaterialTableDemo extends React.Component {
  constructor(props) {
    super(props);
this.state={
  loading:true,
  add:false,
  columns: [

    { title: 'First Name', field: 'firstName' ,cellStyle: {
              fontSize:'12px'
            }},
    { title: 'Last Name', field: 'lastName' , cellStyle: {
              fontSize:'10px'
            }},
    { title: 'Mobile', field: 'mobile',cellStyle: {
              fontSize:'12px'
            }},
            { title: 'Email', field: 'email',cellStyle: {
                      fontSize:'12px'
                    }},
  {title:'Admin permissions', field:'type', lookup: { 1: 'can view', 2: 'view edit',3:'super admin (all permissions)' }},
    { title: 'Admin since', field: 'createdAt',editable: 'never',cellStyle: {
              fontSize:'12px'
            }},
            { title: 'AdminId', field: 'id' ,editable: 'never',cellStyle: {
                      fontSize:'10px'
                    }}


  ],
  data: [


  ],

}
  }

  componentDidMount(){
    axios.post('https://green-planet.herokuapp.com/public/admin/getadmins',{}).then(res=>{
   if(res.data){
     console.log(res.data)
     this.setState({loading:false})
     var userData=[]
     res.data.map(data=>{
       userData.push({email:data.email,id:data._id,createdAt:data.createdAt,firstName:data.firstName,lastName:data.lastName,mobile:data.mobile,type:data.type})
     })
     console.log(userData)
     this.setState({data:userData})
   }

    })
  }


render() {


  return (
    <React.Fragment>

<MaterialTable
    style={{width:'100%'}}
    icons={tableIcons}
    isLoading={this.state.loading}
    options={{
     exportButton: true,
     exportAllData:true

   }}
   editable={{
     onRowAdd: newData =>
       new Promise(resolve => {
         axios.post('https://green-planet.herokuapp.com/public/admin/addadmin',{mobile:newData.mobile,email:newData.email,firstName:newData.firstName,lastName:newData.lastName,displayName:newData.firstName+' '+newData.lastName,type:newData.type}).then((res)=>{
          console.log(res)
           if(res.data.type=='newUser'){
             console.log(newData)
             const data = [...this.state.data];
             data.push({email:res.data.data.email,id:res.data.data._id,createdAt:res.data.data.createdAt,firstName:res.data.data.firstName,lastName:res.data.data.lastName,mobile:res.data.data.mobile,type:res.data.data.type});
             this.setState({ ...this.state, data });

          resolve();
           }
           if(res.data.type=='updated'){

             const data = [...this.state.data];
             data.push({...newData,createdAt:res.data.data.createdAt,id:res.data.data._id});
             this.setState({ ...this.state, data });

          resolve();
           }
           else {
             resolve();
           }
         })


       }),
     onRowUpdate: (newData, oldData) =>
       new Promise(resolve => {
      axios.post('https://green-planet.herokuapp.com/public/admin/updateadmin',{_id:newData.id,email:newData.email,mobile:newData.mobile,firstName:newData.firstName,lastName:newData.lastName,displayName:newData.firstName+' '+newData.lastName,type:newData.type}).then((res)=>{

        if(res.data.nModified==1){
          console.log(newData)
          const data = [...this.state.data];
          data[data.indexOf(oldData)] = newData;
          this.setState({ ...this.state, data });
       resolve();
        }
        else {
          resolve();
        }
      })

    }),
    onRowDelete: oldData =>
          new Promise((resolve, reject) => {

            axios.post('https://green-planet.herokuapp.com/public/admin/updateadmin',{_id:oldData.id,type:0}).then((res)=>{

              if(res.data.nModified==1){
                console.log(res.data)
                  const data = [...this.state.data];
                const index = data.indexOf(oldData);
                data.splice(index, 1);
                this.setState({ data }, () => resolve());
              }
              else {
                resolve();
              }
            })



          })
   }}
    size='large'
      title="Admins"
      columns={this.state.columns}
      data={this.state.data}


    />
    </React.Fragment>
  );
}

}





export default connect()(MaterialTableDemo);
