
const defaultState = {
    appName: '',
    modalMode: false,
    snackbar:
    {open:false,
    sType:'success',
    message:'',}
    ,
    drawer:true,
    loader:false
  };
  
  export default (state = defaultState, action) => {
    switch (action.type) {
      
  
       case 'SNACKBAR':
       return{
         ...state,
         snackbar:action.snackbar
      
       }
  
      default:
        return state;
    }
  };
  