class MaterialTableDemo extends React.Component {
  constructor(props) {
    super(props);
this.state={
  loading:true,
  add:false,
  columns: [
      { title: 'Avatar', field: 'imageUrl', render: rowData => <img src={rowData.imageUrl} style={{width: 40, borderRadius: '50%'}}/> },
    { title: 'First Name', field: 'firstName',editable: 'never' ,cellStyle: {
              fontSize:'12px'
            }},
    { title: 'Last Name', field: 'lastName' ,editable: 'never', cellStyle: {
              fontSize:'10px'
            }},
    { title: 'Mobile', field: 'mobile',editable: 'never' ,cellStyle: {
              fontSize:'12px'
            }},
    { title: 'Customer since', field: 'createdAt',editable: 'never',cellStyle: {
              fontSize:'12px'
            }},
            { title: 'UserId', field: 'id' ,editable: 'never',cellStyle: {
                      fontSize:'10px'
                    }},
  ],
  data: [


  ],

}
  }

  componentDidMount(){
    axios.post('https://green-planet.herokuapp.com/public/admin/getusers',{}).then(res=>{
   if(res.data){
     console.log(res.data)
     this.setState({loading:false})
     var userData=[]
     res.data.map(data=>{
       userData.push({imageUrl:data.provider_pic,id:data._id,createdAt:data.createdAt,firstName:data.firstName,lastName:data.lastName,mobile:data.mobile})
     })
     console.log(userData)
     this.setState({data:userData})
   }

    })
  }


render() {


  return (
    <React.Fragment>

<MaterialTable
    style={{width:'100%'}}
    icons={tableIcons}
    isLoading={this.state.loading}
    options={{
     exportButton: true,
     exportAllData:true

   }}
    size='large'
      title="Customers"
      columns={this.state.columns}
      data={this.state.data}


    />
    </React.Fragment>
  );
}

}





export default connect()(MaterialTableDemo);
