import React,{Component} from 'react';

import Title from './Title';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import MenuItem from '@material-ui/core/MenuItem';
import {UploadEditorImg} from '../utilities/utilFun'
import {updateAttachment,updateProduct,snackbar} from './../actions'
import {connect} from 'react-redux';
import {store} from '../App.js'
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab';
import axios from 'axios'
import  Hidden from '@material-ui/core/Hidden'
import Skeleton from '@material-ui/lab/Skeleton';
import {

    withRouter
  } from "react-router-dom";
class CreateProduct extends Component {
  constructor(props) {
    super(props);

    this.waitf=this.waitf.bind(this)
    this.handleProcessed=this.handleProcessed.bind(this)
    this.handleAttach=this.handleAttach.bind(this)
this.deleteImg=this.deleteImg.bind(this)
this.onCreateProduct=this.onCreateProduct.bind(this)
  }

deleteImg=(index)=>{
  console.log(index)

  var arr=[...this.props.attachment]
  arr.splice(index,1)
  this.props.updateAttachment(arr)
}

handleField=(e)=>{
  var newObj={...this.props.products,[e.target.id]:e.target.value}
  console.log(newObj)
  this.props.updateProduct(newObj)

}

handleCat=(e)=>{
  var newObj={...this.props.products,category:e.target.value}
  console.log(newObj)
  this.props.updateProduct(newObj)
}


onCreateProduct=(e)=>{
  var products=this.props.products
var data={
  title:products.title,
  description:products.description,
  pricing:{
    price:products.price,
    priceVLE:products.priceVLE
  },
  sku:products.sku,
  quantity:products.quantity,
 category:products.category,
 images:[]
}

this.props.attachment.map((image)=>{
  data.images.push(image.url)
})
console.log(data)

if(this.props.mode==2){
  axios.post('https://green-planet.herokuapp.com/public/product/update',{_id:this.props.id,...data}).then(res=>{
    console.log(res.data)
    if (res.data){

    }
  })
}
else{
  this.props.snackbar({sType:'info',open:true,message:'Adding '+data.title})
  axios.post('https://green-planet.herokuapp.com/public/product/add',{...data}).then(res=>{
    console.log(res.data)
    if (res.data){
    this.props.updateAttachment([])
    this.props.updateProduct({title:null,
    _id:null,
    description:null,
    category:null,
    price:null,
    priceVLE:null,
    quantity:null,
    sku:null})
    this.props.snackbar({sType:'success',open:true,message:'Successfuly added '+ res.data.title})
    this.props.history.push('/products')
    }
  }).catch(e=>{
      this.props.snackbar({sType:'error',open:true,message:'Opps something went wrong!'})

  })
}
}

  handleAttach=(e)=>{

        var file=e.target.files[0]
          var type=file.name.split(".")

          console.log(file)
          var id=Date.now()
          var arr=[]
         arr=  [...this.props.attachment]
          var arrele={
          id:id,
          status:'queue',
          file:file,
          name:file.name,
          url:'',
          type:type.pop(),
          mime:file.type,
          }
          arr.push({...arrele})

          this.props.updateAttachment(arr)
          console.log(this.props.attachment)
      }

      handleProcessed(resolve){
console.log(resolve)
      var arr=[...this.props.attachment]
      var indexUP=arr.findIndex(
          item => item.id === resolve.id
      );
      arr[indexUP].status='processed';
      arr[indexUP].url=resolve.data.data.Location;
      console.log( arr[indexUP])

      this.props.updateAttachment(arr);
      console.log('processed')
    }




        waitf(value){
console.log('waitif')

    return new Promise(function (resolve, reject){
      UploadEditorImg(value.file,{ext:value.type,mime:value.mime})
      .then((data)=>resolve({id:value.id,data}))



    })
        }

        componentDidMount(){
          if(this.props.mode==2){
            axios.get('https://green-planet.herokuapp.com/public/productDetail/'+this.props.id).then(res=>{
if(res.data){
  console.log(res.data)
  var images=[]
  this.props.updateProduct({...res.data[0],...res.data[0].pricing})
  res.data[0].images.map(data=>{
    images.push({status:'processed',url:data})
  })
  console.log(images)
  this.props.updateAttachment(images)
}

            })




          }
        }


render(){

  var data=[]
  console.log(this.props)
var arr=this.props.attachment
arr.map((value)=>{
  console.log(value)
  if(value.status=='queue'){
    console.log('queue')
      this.waitf(value).then((resolve)=>{this.handleProcessed(resolve)})
      value.status='processing'
}
data.push(value)
})

  return (
    <React.Fragment>


      <form >
      <Grid container spacing={3} justify={'center'}>
      <Grid item xs={12}><Title>{this.props.mode==2?'Edit Product':'Add Product'}</Title></Grid>


      <Grid item xs={12}><TextField
      id="title"
      fullWidth
      InputLabelProps={{ shrink: true }}
      value={this.props.products.title}
      label="Title of product"
      margin="normal"
      onChange={this.handleField}
      variant="outlined"
      ></TextField></Grid>



      <Grid item xs={12}><TextField
      id="sku"
      fullWidth
      InputLabelProps={{ shrink: true }}
      value={this.props.products.sku}
      label="SKU of product"
      margin="normal"
      variant="outlined"
      onChange={this.handleField}
      ></TextField></Grid>




      <Grid item xs={12}>


      <FormControl margin='normal'   id= 'category' fullWidth>
             <InputLabel shrink htmlFor="age-helper" margin='normal'>category</InputLabel>
             <Select
             label='product category'
               margin="normal"
                  id= 'category'
                  value={this.props.products?this.props.products.category:""}
onChange={this.handleCat}
      variant={'outlined'}
      input={<Input name="age" id="age-helper" />}
               inputProps={{
                 name: 'category',

               }}
             >
               <MenuItem value="">
                 <em>None</em>
               </MenuItem>
               <MenuItem id= 'category' value={10}>Ten</MenuItem>
               <MenuItem value={20}>Twenty</MenuItem>
               <MenuItem value={30}>Thirty</MenuItem>
             </Select>

             </FormControl>

      </Grid>

      <Grid item xs={12}>
       <InputLabel shrink htmlFor="age-helper" margin='normal'>Add Images</InputLabel>
      <Grid container spacing={3} justify='left'>
        {
          this.props.attachment.map((value,index)=>{
            if(value.status=='processed')
             return(<Grid item  >
            <div style={{width:'100%',height:'100%',position:'relative',padding:'30px',border:'1px solid #e0e0e0',borderRadius:'8px'}}>
              <img src={value.url} style={{height:'200px',width:'auto'}}/>
              <IconButton onClick={()=>this.deleteImg(index)} aria-label="delete" color='secondary' style={{position:'absolute',right:0,top:0}}>
                        <DeleteIcon fontSize="small" />
                      </IconButton>
            </div>
              </Grid>
)

else{

  return(
    <Grid item  >
   <div style={{width:'100%',height:'100%',position:'relative',padding:'30px',border:'1px solid #e0e0e0',borderRadius:'8px'}}>
     <Skeleton height="200px" width={"150px"} />
     <IconButton onClick={()=>this.deleteImg(index)} aria-label="delete" color='secondary' style={{position:'absolute',right:0,top:0}}>
               <DeleteIcon fontSize="small" />
             </IconButton>
   </div>
     </Grid>
  )
}
          })

        }


        <Grid item xs={6} lg={3} style={{width:'200px',height:'200px'}} justify='center'>
        <center>  <Fab color="primary" aria-label="add" style={{marginTop:'70px'}} onClick={()=>document.getElementById('addImage').click()}>
      <AddIcon /> </Fab></center>
        </Grid>
      </Grid>
      </Grid>

      <Grid item xs={12}>
      <TextField
       id="description"
       InputLabelProps={{ shrink: true }}
       label="description"
       value={this.props.products.description}
       multiline
       onChange={this.handleField}
       rows="4"
       defaultValue="Describe your product here"
fullWidth
       margin="normal"
       variant="outlined"
     />
      </Grid>

      <Grid item xs={6} lg={4}>
      <TextField
      id="quantity"
      fullWidth
      InputLabelProps={{ shrink: true }}
      label="Quantity Available"
      margin="normal"
      onChange={this.handleField}
      type='number'
      value={this.props.products.quantity}
      variant="outlined"
      ></TextField>
      </Grid>

      <Grid item xs={6} lg={4}>
      <TextField
      id="price"
      fullWidth
      value={this.props.products.price}
      onChange={this.handleField}
      label="Price of product"
      margin="normal"
      InputLabelProps={{ shrink: true }}
      type='number'
      variant="outlined"
      ></TextField>
      </Grid>


      <Grid item xs={6} lg={4}>
      <TextField
      id="priceVLE"
      fullWidth
      type='number'
      InputLabelProps={{ shrink: true }}
      value={this.props.products.priceVLE}
      onChange={this.handleField}
      label="Price for VLE"
      margin="normal"
      variant="outlined"
      ></TextField>
      </Grid>

<Grid item xs={12}>
<Button variant='contained' fullWidth color='primary' onClick={this.onCreateProduct} style={{padding:"15px"}}><AddIcon style={{marginRight:'15px'}}/>{this.props.mode==2?'Update Product':'Create Product'}</Button>
</Grid>
      </Grid>
      </form>
<Hidden ><input style={{visibility:'hidden'}} type='file' id='addImage'  ref="Upload" onChange={this.handleAttach} /></Hidden>

    </React.Fragment>
  );
}

}


const mapStateToProps = state => {

    return {
      attachment:state.product.attachment,
      products:state.product.products
     }
};

export default withRouter(connect(mapStateToProps,{updateAttachment,updateProduct,snackbar})(CreateProduct));
